@import url(https://fonts.googleapis.com/css2?family=Matemasie&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'CustomFont';
  src: url(/gender-reveal/static/media/JasonHandwriting2.2d9352a0.ttf) format('truetype');
  /* Add additional font formats if necessary */
  /* Specify the correct path to the font files */
}

#hint {
  font-family: 'CustomFont', sans-serif;
  text-align: left;
}

#hintTitle {
  font-size: 20px;
}

.title {
  font-family: "Matemasie", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
  margin-bottom: 36px;
}

.hint-sector {
  display: flex;
  grid-gap: 16px;
  gap: 16px;
}

#container {
  height: 100vh;
  padding-top: 36px;
  background-color: #f8f6ee;
}
#gender-radios {
  margin-top: 16px;
  grid-gap: 20px;
  gap: 20px;
}
.gender-label {
  width: 140px;
  height: 140px;
  display: flex !important;
  align-items: center;
  justify-content: center;
  color: white !important;
  font-weight: 600 !important;
}
#girl ~ label {
  background-color: #EECAD5;
}
#girl:checked ~ label {
  background-color: #d6b5bf;
  border: 2px solid #bea1aa;
}
#boy ~ label {
  background-color: #D1E9F6;
}
#boy:checked ~ label {
  background-color: #bcd1dd;
  border: 2px solid #a7bac4;
}
#submitBtn {
  border-radius: 30px;
  display: flex;
  grid-gap: 8px;
  gap: 8px;
  width: 100%;
  justify-content: center;
}
.count-down-timer {
    width: 100%;
    height: 100%;
    display: flex;
    margin-top: 40%;
    justify-content: center;
}
